function getCookie(name)
{
var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
if(arr=document.cookie.match(reg))
return unescape(arr[2]);
else
return null;
}

function setCookie(name,value)
{

var Days = 30;
var exp = new Date();
exp.setTime(exp.getTime() + Days*24*60*60*1000);

document.cookie = name + "="+ escape (value) + ";expires=" + exp.toGMTString();
}


if(getCookie("name")==null){
var obj={};
obj.phone=""
obj.password=""
setCookie("name",JSON.stringify(obj))
vuex_store.state.phone=obj.phone
vuex_store.state.password=obj.password
console.log(1)
}else{

var newObj=JSON.parse(getCookie("name"))
vuex_store.state.phone=newObj.phone
vuex_store.state.password=newObj.password

}
if(vuex_store.state.phone!=""&&vuex_store.state.password!=""){
$.ajax({
type:"post",
url:"https://www.xueguoguo.cn/api/v1/user/login",
async:false,
data:JSON.stringify({phone:vuex_store.state.phone,password:vuex_store.state.password})
}).done(function(result){
console.log(result)
vuex_store.state.token=result.data.token
vuex_store.state.isVip=result.data.isvip
})
}










