
import Zepto from '../../static/zepto.min.js'
import Progress from '../../static/progress.js'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"ProSelect",
components:{
ViewBox
},
data:function(){
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options
disable:false,//data.vuegConfigdisablefalse
backAnim:'slideInLeft'
},

SchoolArr1: [],
school:true,
noData:false
}
},
mounted(){

var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/recommandMajors?college_id="+this.$store.state.VolId,{})
.then(function(result){

if(result.data.msg==""){
_this.school=false
_this.noData=true
}else{
_this.school=true
_this.noData=false
_this.$nextTick(function(){
_this.draw();
})
}
})


},
methods:{

draw() {
$(".myChart").each(function() {
$(this).ringChart();
})
}
}
}



